<script>
    import MyCouseLists from "./MyCouseLists.vue";

    export default {
        data() {
            return{
                courses: [
                    { code: 'APSC 111', name: 'Physics I', state: 'completed', semester: 'Fall'},
                    { code: 'APSC 131', name: 'Chemistry and Materials', state: 'completed', semester: 'Fall'},
                    { code: 'APSC 143', name: 'Introduction to Computer Programming for Engineers', state: 'completed', semester: 'Winter'},
                    { code: 'APSC 171', name: 'Calculus I', state: 'completed', semester: 'Fall'},
                    { code: 'CMPE 251', name: 'Data Analytics', state: 'inProgress', semester: 'Fall'},
                    { code: 'CMPE 457', name: 'Image Processing', state: 'inProgress', semester: 'Winter'},
                    { code: 'ELEC 490', name: 'Capstone', state: 'inProgress', semester: 'Fall/Winter'},
                    { code: 'ELEC 371', name: 'Microprocessor Interfacing and Embedded Systems', state: 'inProgress', semester: 'Fall'},
                    { code: 'ELEC 299', name: 'Mechatronics', state: 'inProgress', semester: 'Winter'},
                    { code: 'CMPE 458', name: 'Programming Language Processors', state: 'saved', semester: 'Winter'}
                ],
                courseLists: [
                    { name: 'Completed Courses'},
                    { name: 'Current Courses'},
                    { name: 'Saved For Later'}
                ],
                semesterOptions: [
                    { name: 'Fall/Winter' },
                    { name: 'Fall' },
                    { name: 'Winter' }
                ],
                panel: [1,2]
            }
        },
        components: {
            MyCouseLists
        },
        computed: {
            CompletedCourses(){
                return this.courses.filter(course => {
                    return course.state === 'completed'
                })
            },
            InProgress(){
                return this.courses.filter(course => {
                    return course.state === 'inProgress'
                })
            },
            YearInProgress(){
                return this.courses.filter(course => {
                    return course.state === 'inProgress' && course.semester === 'Fall/Winter'
                })
            },
            FallInProgress(){
                return this.courses.filter(course => {
                    return course.state === 'inProgress' && course.semester === 'Fall'
                })
            },
            WinterInProgress(){
                return this.courses.filter(course => {
                    return course.state === 'inProgress' && course.semester === 'Winter'
                })
            },
            YearSaved(){
                return this.courses.filter(course => {
                    return course.state === 'saved' && course.semester === 'Fall/Winter'
                })
            },
            FallSaved(){
                return this.courses.filter(course => {
                    return course.state === 'saved' && course.semester === 'Fall'
                })
            },
            WinterSaved(){
                return this.courses.filter(course => {
                    return course.state === 'saved' && course.semester === 'Winter'
                })
            }
        }
    }
</script>
